
.finder-box {
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 5px;
    padding: 10px 50px 30px 50px;
    margin-top: 100px;
}
h1 {
    margin-bottom: 50px;
}
p {
    margin-left: 5px;
}
.city {
    color: #444;
    font-size: 27px;
    font-weight: bold;
}
.error {
      color: darkred;
      font-size: 15px;
}
.form-control {
    margin-bottom: 10px;
}
.find-button {
    margin: 25px auto 40px auto;
    color: #fff;
    background-color: #33b797;
    border-color: #33b797;
}
.find-button:focus {
      outline: none;
}




